import React from 'react';
import Lottie from 'lottie-react';
import aniDev from './dev.json';
import './Home.css';
import '../global.css';
import 'bulma/css/bulma.min.css';
import SocialLinks from './SocialLink';

const style = {
  height: '100%',
  width: '100%',
};

export default function Home() {
  return (
    <>
      <div className="main_layout">
        <div className="div-top" class="row">
          <div class="col">
            <section class="hero is-warning">
              <section class="section is-large">
                <h1 className="home_title_L"></h1>
                <h1 className="home_title_L"> </h1>
                <h1 className="home_title_L">
                  <strong></strong>.
                </h1>
                <div className="term-contents1" />
                <h2 class="home_subTitle_L">
                  <strong> </strong>
                </h2>
                <h2 class="home_subTitle_L">
                  
                </h2>
                <h2 class="home_subTitle_L">
                  <strong> </strong>
                </h2>
                <h2 class="home_subTitle_L">
                     
                </h2>
              </section>
            </section>
          </div>
          <div class="col">
            <section class="hero is-success">
              <section class="section is-large">
                <h1 class="home_title_R">  </h1>
                <h1 class="home_title_R2">.</h1>
                <div className="term-contents1" />
                <h2 class="home_subTitle_R"> </h2>
                <h2 class="home_subTitle_R">
                    <strong></strong>{' '}
                </h2>
                <h2 class="home_subTitle_R">
                  <strong></strong>   
                </h2>
                <div className="socialLink-div">
                  <div className="term-contents" />
                  <SocialLinks />
                </div>
              </section>
            </section>
          </div>
          <div class="col">
            <Lottie
              animationData={aniDev}
              style={style}
              className="animation"
            />
          </div>
        </div>
        <div className="socialLink-div"></div>
      </div>
    </>
  );
}

