import React from 'react';
import '../global.css';
import './Profile.css';
import profileImage from './profileEdit.png';
import Lottie from 'lottie-react';
import arrowLtoR from './arrowLtoR.json';

const Info = [
  {
    name: '',
    Email: 'dayeee5@naver.com',
    Phone: '010-2426-9940',
    birth: '2000.02.25',
    education: ' (2016.03 ~ 2019.01)',
    educationTwo: '  (2022.03 ~ 2026.02)',
    intership: ' 2025 Summer Tech Intership',
    partTimeJob: '   (2022.10 ~ 2023.02)',
    awards: 'SW    (, 2024)',
    certifications: ' ',
    certifications_Sec: '  2',
    certifications_Third: 'OCP(Oracle Certified Professional)',
    certifications_Four: '   2',
    languageScores: 'TOEIC 990',
  },
];

export default function Profile() {
  const skills = [
    { skill: 'Python', level: 90 }, // : 0 100 
    { skill: 'C', level: 70 },
    { skill: 'C++', level: 80 },
    { skill: 'Java', level: 90 },
    { skill: 'React', level: 90 },
  ];
  return (
    <div className="main_layout">
      <div className="page_title">
        Resume
        <div className="Title_line" />
      </div>
      <div class="row">
        <Lottie animationData={arrowLtoR} className="arrowLtoR" />
        {/*  &   (Table) */}
        <div class="col">
          <div className="resume-container">
            <table className="resume-table">
              <thead>
                <tr>
                  <th></th>
                  <th> </th>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td>
                    <img
                      src={profileImage}
                      alt={`  - ${Info[0].name}`}
                      className="profile-image"
                    />
                  </td>
                  <td className="profile-info">
                    <div className="betweenLine">
                      <strong>  | </strong> {Info[0].name}
                    </div>
                    <div className="betweenLine">
                      <strong>Email | </strong> {Info[0].Email}
                    </div>
                    <div className="betweenLine">
                      <strong> | </strong> {Info[0].Phone}
                    </div>
                    <div className="betweenLine">
                      <strong> | </strong> {Info[0].birth}
                    </div>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
        {/* ,    */}
        <div class="row">
          <div class="col"></div>
          <div class="col">
            <div className="experience-container">
              <table className="experience-table">
                <thead>
                  <tr>
                    <th></th>
                    <th></th>
                  </tr>
                </thead>
                <tbody>
                  <tr>
                    <td></td>
                    <td>{Info[0].education}</td>
                  </tr>
                  <tr>
                    <td class="sr-only">2</td>
                    <td>{Info[0].educationTwo}</td>
                  </tr>
                  <tr>
                    <td></td>
                    <td>{Info[0].intership}</td>
                  </tr>
                  <tr>
                    <td></td>
                    <td>{Info[0].partTimeJob}</td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
          {/*   */}
          <div class="col">
            <div className="result-container">
              <table className="result-table">
                <thead>
                  <tr>
                    <th></th>
                    <th></th>
                  </tr>
                </thead>
                <tbody>
                  <tr>
                    <td></td>
                    <td>{Info[0].awards}</td>
                  </tr>
                  <tr>
                    <td></td>
                    <td>{Info[0].certifications}</td>
                  </tr>
                  <tr>
                    <td className="sr-only"></td>
                    <td>{Info[0].certifications_Sec}</td>
                  </tr>
                  <tr>
                    <td className="sr-only"></td>
                    <td>{Info[0].certifications_Third}</td>
                  </tr>
                  <tr>
                    <td className="sr-only"></td>
                    <td>{Info[0].certifications_Four}</td>
                  </tr>
                  <tr>
                    <td></td>
                    <td>{Info[0].languageScores}</td>
                  </tr>
                  <tr>
                    <td></td>
                    <td>
                      {skills.map((skill, index) => (
                        <div key={index}>
                          {skill.skill}: {skill.level}%
                        </div>
                      ))}
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
      {/*   */}
      <div className="intro-bg">
        <div className="intro_title">
          Introduce
          <div className="Title_line" />
        </div>
        <div class="row" className="intro-scope">
          <div class="col">
            <div className="intro-list">
              <article class="message is-warning">
                <div class="message-header">
                  <p> </p>
                  <button class="delete" aria-label="delete"></button>
                </div>
                <div class="message-body">
                           
                     .   , 
                         
                  .
                </div>
              </article>
              <article class="message is-warning">
                <div class="message-header">
                  <p>  </p>
                  <button class="delete" aria-label="delete"></button>
                </div>
                <div class="message-body">
                  IT        .
                      ,   
                     .   
                     ,     
                       .
                </div>
              </article>
              <article class="message is-warning">
                <div class="message-header">
                  <p>   </p>
                  <button class="delete" aria-label="delete"></button>
                </div>
                <div class="message-body">
                        
                    .     
                  ,       ,
                        .
                </div>
              </article>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
